@import "typography.scss";
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&family=Roboto:wght@500&display=swap');


* { box-sizing: border-box; }

/* Navigation bar setup dimensions and colour */
nav {
    background-color: #FFDE59;
    padding:10px 15px 10px 15px;
    height: 6vh;
}

/* Navigation bar container for textarea and pencil icon, justifying element to center */
.topNavigation {
    margin-top: 5px;
    display: flex;
    justify-content: center;
}

/* Positioning for pencil icon */
.topNavigation i{
    margin-top: 10px;
}

/* Styling elements for STUDBUD logo */
#logo{ 
    font-size: 42px;
    text-transform: uppercase;
    text-decoration: none;
    font-family: "Poppins", sans-serif;
    font-weight: 700;
    color: black;
    position: absolute;
    top:0;
}

/* Styling elements for Textarea box */
.textarea {
    background-color: #FFDE59;
    border-bottom: dotted;
    border-top: none;
    border-left: none;
    border-right: none;
    resize: none;
    white-space: nowrap;
    overflow-x: scroll; 
    width: 40vw;
    height: 3vh;
}
// Music Player//

.music-container{
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    
}

.imageWrapper{
    display: flex;
    align-items: center;
    flex-direction: column;
    cursor: pointer;
}

.musicImage img {
    height: 80px;
    width:100px;
    border-radius: 5%;
    
}

.musicImage{
    border-radius: 5%;
    padding: 10px;
    text-align: center;
    overflow: hidden;
}

.imageWrapper h2{
    margin-top: 1rem;
    font-size: 1.5rem;
    text-transform: capitalize;
}

.imageWrapper h6{
    font-size: 0.9rem;
    margin-top: 0;
    font-weight: 400;
    color: grey;
}


.musicProgress{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    width: 60%;
}

.progress{
    grid-column: 1 / -1;
    height: 0.4rem;
    margin: 0.8rem;
    cursor: pointer;
    position: relative;
    background-color: aqua;
}

.progressBar{
  background-color: #fafafa;  
  height: 100%;
  width: 100%;
  position: relative;
  border-radius: 0.8rem;

}

.progressHead{
    position: absolute;
    background-color: #fafafa;
    height: 1.2rem;
    width: 1.2rem;
    top: 0;
    left: 0;
    transform: translate(-50%, -32%);
    border-radius: 100%;
    z-index: 2;
    box-shadow: 0 0 0 3px grey;
}

.currentTime{
    text-align: left;
    margin-left: 0.5rem;
}

.duration {
    text-align: end;
    margin-right: 0.5rem;
}

.musicButtons{
    margin-top: 25px;
    width: 300px;
    display: inline-flex;
    align-items: center;
    justify-content: space-between;
}

.musicButtons button{
    cursor: pointer;
    border: unset;
    box-shadow: unset;
    background-color: unset;
    font-size: 1.4rem;  
}

.playSong{
    background: linear-gradient(to right, #425ef0, #7854fb);
    text-align: center;
    border-radius: 100%;
    padding: 0.3rem 0.3rem;
}





//Side Bar Tabs//
.tabs{
float: left;
width: 20%;
height: 1000px;
margin-top: 12px;
}

.tabs a{
    text-decoration: none;  
}

.tabs button{
  display: block; 
  color: black;
  padding:  18px 16px;
  width: 70%;
  outline: none;
  border: none;
  text-align: left;
  cursor: pointer;
  transition: 0.5 ease-in;
  font-size: 17px;
  margin: 15px;
}

.tabs button:hover{
  background-color: lightgray;  
}

.tabs button:active {
    background-color: white; 

}

.timerContent{
    font-family: "Poppins", sans-serif;
}

.timerTabs{
    
    display: flex;
    justify-content: flex-start;
}

.timerTabs button{
  
  color: black;
  padding: 10px;
  width: 15%;
  outline: none;
  border: none;
  border-radius: 15px;
  text-align: center;
  cursor: pointer;
  transition: 0.5 ease-in;
  font-size: 17px;
  margin: 15px;
  margin-left: 0;
}

.timerContent h1{
    font-size: 32px;
    text-transform: uppercase;
    margin-bottom: 0;
}

.stopwatchContainer{
   background-color: lightgray;
   display: flex;
   justify-content: center;
   align-items: center;
   flex-direction: column;
   height: 550px;
}

.stopwatch{
    color: black;
    font-size: 84px;
    font-weight: 700;
}

.stopwatchButtons button{
    background-color: #C9E265;
    border: none;
    border-radius: 10px;
    padding: 10px;
    font-size: 18px;
    margin: 0 10px;
    cursor: pointer;
}

.pomodoroContainer{
    background-color: lightgray;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(3, 1fr);
    height: 550px;
}

.label {
    align-self: center;
    justify-self: center;

    font-size: 30px;
    font-weight: bold;
}

#session{
    grid-area: 1 / 2 / 1 / 2;
}
#Interval {
    grid-area: 1 / 4 / 1 / 4;
}
#break {
    grid-area: 1 / 3 / 1 / 3;
}

.pomodoro{
    display: flex;
    align-self: center;
    justify-self: center;

    font-size: 30px;
    font-weight: bold;
}

p {
    margin: 0;
    padding: 0;
}

#breakTimer {
    grid-area: 2 / 3 / 2 / 3;
}

#sessionTimer{
    grid-area: 2 / 2 / 2 / 2;
}
#counter{
    grid-area: 2 / 4 / 2 / 4;
}

.PomodoroButtons button{
    display: grid;
    background-color: #C9E265;
    border: none;
    border-radius: 10px;
    padding: 10px;
    font-size: 18px;
    margin: 0 10px;
    cursor: pointer;
    display: block;
    align-self: center;
    justify-self: center;
}

#Start {
    grid-area: 3 / 2 / 3 / 2;
}

#Stop {
    grid-area: 3 / 3 / 3 / 3;
}

#Reset {
    grid-area: 3 / 4 / 3 / 4;
}
